import React, { useState } from "react";
import wrapper from "../commons/WebComponentWrapper";

const Node = ({ node, onClick }) => (
    <div onClick={() => onClick(node)} style={{ paddingLeft: 10 }}>
        {node.name}
        {node.children && node.children.map(childNode => (
            <Node node={childNode} onClick={onClick} />
        ))}
    </div>
);


const Tree = ({ data, onClick }) => (
    <Node node={data} onClick={onClick} />
);

const ContentTree = function (props, ref) {
    const [selectedNode, setSelectedNode] = useState(null);

    const data = {
        name: 'Root',
        children: [
            {
                name: 'Child 1',
                children: [
                    { name: 'Grandchild 1' },
                    { name: 'Grandchild 2' },
                ],
            },
            { name: 'Child 2' },
        ],
    };

    return (
        <div>
            <Tree data={data} onClick={setSelectedNode} />
            {selectedNode && (
                <div style={{ marginTop: 10 }}>
                    Selected node: {selectedNode.name}
                </div>
            )}
        </div>
    );
}

const styles = `

`

const props = [{
    name: "message",
    defaultValue: "Click me",
    type: "string"
}]

const WrappedComponent = wrapper(React.forwardRef(ContentTree), props);
export { WrappedComponent, styles, props };